import { useState, useEffect, useRef } from "react";
import Image from "next/image";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { Menu, X } from "lucide-react";
import { ArrowUpRight } from "lucide-react";
import { ThemeToggle } from "@/components/ThemeToggle";

interface NavigationProps {
  currentSection: string;
  onSectionChange: (section: string) => void;
}

export default function Navigation({ currentSection, onSectionChange }: NavigationProps) {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const mobileMenuRef = useRef<HTMLDivElement>(null);

  const sections = [
    { id: "services", label: "Services" },
    { id: "solutions", label: "Solutions" },
    { id: "work", label: "Work" },
    { id: "about", label: "About" }
  ];

  const handleNavClick = (sectionId: string) => {
    // Smooth scroll behavior is handled by CSS (scroll-behavior: smooth in globals.css)
    onSectionChange(sectionId);
    setIsMobileMenuOpen(false);
    
    // Scroll to top smoothly when navigating
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  // Close mobile menu on Escape key
  useEffect(() => {
    const handleEscape = (e: KeyboardEvent) => {
      if (e.key === 'Escape' && isMobileMenuOpen) {
        setIsMobileMenuOpen(false);
      }
    };

    document.addEventListener('keydown', handleEscape);
    return () => document.removeEventListener('keydown', handleEscape);
  }, [isMobileMenuOpen]);

  // Trap focus in mobile menu when open
  useEffect(() => {
    if (isMobileMenuOpen && mobileMenuRef.current) {
      const focusableElements = mobileMenuRef.current.querySelectorAll(
        'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])'
      );
      const firstElement = focusableElements[0] as HTMLElement;
      const lastElement = focusableElements[focusableElements.length - 1] as HTMLElement;

      const handleTabKey = (e: KeyboardEvent) => {
        if (e.key !== 'Tab') return;

        if (e.shiftKey) {
          if (document.activeElement === firstElement) {
            lastElement?.focus();
            e.preventDefault();
          }
        } else {
          if (document.activeElement === lastElement) {
            firstElement?.focus();
            e.preventDefault();
          }
        }
      };

      document.addEventListener('keydown', handleTabKey);
      return () => document.removeEventListener('keydown', handleTabKey);
    }
  }, [isMobileMenuOpen]);

  return (
    <nav className="fixed top-4 left-1/2 -translate-x-1/2 z-50 w-[95%] max-w-5xl" aria-label="Main navigation">
      {/* Glassmorphic Pill Container */}
      <div className="bg-background/80 dark:bg-background/90 backdrop-blur-xl border border-border/50 dark:border-border rounded-full shadow-2xl dark:shadow-[0_0_30px_rgba(0,0,0,0.5)] px-6 py-3 animate-fade-in-down">
        <div className="flex justify-between items-center">
          {/* Logo */}
          <div className="flex-shrink-0 flex items-center gap-2">
            <Image
              src="/PIC-A-BOARD_logo_w.png"
              alt="PICABORD Logo"
              width={28}
              height={28}
              className="h-5 w-auto md:h-6 invert dark:invert-0 transition-[filter] duration-300 ease-in-out"
              priority
            />
            <h1 className="text-lg md:text-xl font-medium text-foreground font-picabord tracking-tight pt-1">
              PICABORD
            </h1>
          </div>

          {/* Desktop Navigation */}
          <div className="hidden md:flex items-center space-x-1" role="list">
            {sections.map((section) => (
              <Link key={section.id} href={(section.id === 'home' ? '/' : `/${section.id}`) as any}>
                <Button
                  variant="ghost"
                  size="sm"
                  data-testid={`nav-${section.id}`}
                  aria-current={currentSection === section.id ? 'page' : undefined}
                  className={`relative px-5 py-3 text-base font-semibold transition-all duration-200 rounded-full focus-visible-ring ${
                    currentSection === section.id
                      ? "text-primary-foreground bg-primary hover:bg-primary/90"
                      : "text-muted-foreground hover:text-foreground hover:bg-accent/50"
                  }`}
                >
                  {section.label}
                  {/* Active indicator underline */}
                  {currentSection === section.id && (
                    <span className="absolute bottom-0 left-1/2 -translate-x-1/2 w-1/2 h-1 bg-primary-foreground rounded-full" />
                  )}
                </Button>
              </Link>
            ))}
          </div>

          {/* Theme Toggle, Contact CTA & Mobile Menu Button */}
          <div className="flex items-center gap-2">
            <ThemeToggle />

            {/* Contact Us - desktop */}
            <div className="hidden md:block">
              <Link href={"/contact" as any} className="group">
                <button
                  className="inline-flex items-center gap-3 px-4 py-2 rounded-full transition-all duration-300 focus-visible-ring"
                  aria-label="Contact Us"
                >
                  <span className="text-sm font-medium text-muted-foreground transition-colors duration-300 group-hover:text-primary-foreground">
                    Contact Us
                  </span>
                  <span className="inline-flex items-center justify-center h-7 w-7 rounded-md bg-primary/10 text-primary transition-colors duration-300 group-hover:bg-primary group-hover:text-primary-foreground shadow-sm">
                    <ArrowUpRight className="h-4 w-4" />
                  </span>
                </button>
              </Link>
            </div>

            <div className="md:hidden">
              <Button
                variant="ghost"
                size="icon"
                onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
                data-testid="button-mobile-menu"
                aria-label={isMobileMenuOpen ? "Close menu" : "Open menu"}
                aria-expanded={isMobileMenuOpen}
                className="rounded-full focus-visible-ring"
              >
                {isMobileMenuOpen ? <X className="h-5 w-5" /> : <Menu className="h-5 w-5" />}
              </Button>
            </div>
          </div>
        </div>
      </div>

      {/* Mobile Navigation Dropdown */}
      {isMobileMenuOpen && (
        <div 
          ref={mobileMenuRef}
          className="md:hidden mt-2 animate-fade-in-down"
          role="dialog"
          aria-label="Mobile navigation menu"
        >
          <div className="bg-background/80 dark:bg-background/90 backdrop-blur-xl border border-border/50 dark:border-border rounded-3xl shadow-2xl dark:shadow-[0_0_30px_rgba(0,0,0,0.5)] px-4 py-3 space-y-1">
            {sections.map((section, index) => (
              <Link key={section.id} href={(section.id === 'home' ? '/' : `/${section.id}`) as any}>
                <Button
                  variant="ghost"
                  size="sm"
                  data-testid={`nav-mobile-${section.id}`}
                  onClick={() => setIsMobileMenuOpen(false)}
                  aria-current={currentSection === section.id ? 'page' : undefined}
                  className={`w-full justify-start px-5 py-3 text-base font-medium rounded-full transition-all duration-200 focus-visible-ring animate-fade-in-up ${
                    currentSection === section.id
                      ? "text-primary-foreground bg-primary hover:bg-primary/90"
                      : "text-muted-foreground hover:bg-accent/50"
                  }`}
                  style={{ animationDelay: `${index * 50}ms` }}
                >
                  {section.label}
                </Button>
              </Link>
            ))}
            {/* Mobile Contact CTA */}
            <div className="pt-2">
              <Link href={'/contact' as any}>
                <button
                  className="w-full inline-flex items-center justify-center gap-3 px-5 py-3 rounded-full bg-primary/10 text-primary transition-colors duration-300 hover:bg-primary hover:text-primary-foreground focus-visible-ring"
                  onClick={() => setIsMobileMenuOpen(false)}
                  aria-label="Contact Us"
                >
                  <span className="text-sm font-medium">Contact Us</span>
                  <span className="inline-flex items-center justify-center h-7 w-7 rounded-md bg-primary text-primary-foreground">
                    <ArrowUpRight className="h-4 w-4" />
                  </span>
                </button>
              </Link>
            </div>
          </div>
        </div>
      )}
    </nav>
  );
}